.site-container {
  overflow: hidden;
  min-width: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1770px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: none;
  }
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  overflow: hidden;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
}

.main {
  padding: 50px;
}

.goods {
  display: flex;
  margin: 0 -17.5px;
}

.goods__column {
  flex: 0 1 33.333%;
  padding: 0 17.5px;
}

.goods__item {
  position: relative;
  border-radius: 25px;
  padding: 31px 37px;
  background: #F8FAFC;
  transition: all 0.8s ease 0s;
}

.goods__item:hover {
  -moz-box-shadow: 0 4px 42px 13px rgba(153, 175, 178, 0.43);
  -webkit-box-shadow: 0 4px 42px 13px rgba(154, 188, 192, 0.43);
  box-shadow: 0 4px 42px 13px rgba(153, 175, 178, 0.43);
}

.goods__top {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.goods__status {
  position: absolute;
  left: 5%;
  top: 50px;
  padding: 0 20px;
  font-weight: 500;
  font-size: 14px;
  line-height: 26px;
}

.goods__libra {
  position: absolute;
  right: 5%;
  top: 40px;
  border-radius: 30px;
  width: 46px;
  height: 46px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  background: #fff;
}

.goods__libra svg {
  height: 100%;
  max-width: 100%;
}

.goods__image {
  position: relative;
  margin-bottom: 25px;
  width: 263px;
  height: 260px;
}

.goods__image:hover .goods__image-top {
  z-index: 0;
  opacity: 0;
}

.goods__image-top {
  position: absolute;
  top: 0;
  z-index: 1;
  transition: all 0.8s ease 0s;
}

.goods__image-bottom {
  position: absolute;
  top: 0;
}

.goods__image img {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
}

.goods__title {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 25px;
}

.goods__descr {
  margin-bottom: 20px;
}

.goods__descr p {
  font-size: 16px;
  line-height: 24px;
  color: #2C2C2C;
}

.goods__options {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.goods__spoiler {
  position: relative;
  border-radius: 30px;
  padding: 10px 5px 10px 0;
  width: 50%;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  background: #fff;
  cursor: pointer;
}

.goods__spoiler:before {
  content: "";
  position: absolute;
  background: url("../img/sprite.svg#arrow") 0 0 no-repeat;
  width: 15px;
  height: 13px;
  right: 7%;
  top: 15px;
}

.goods__price {
  font-weight: 600;
  font-size: 18px;
}

.goods__footer {
  display: flex;
}

.goods__btn-buy {
  flex: 0 1 70%;
  margin-left: 31px;
  font-weight: 600;
  font-size: 18px;
  line-height: 39px;
}

.goods__btn-buy:hover {
  background: linear-gradient(261.65deg, #E9B32C -3.95%, #BF911A 109.44%);
}

.goods__item-buy {
  display: flex;
  flex-direction: column;
}

.goods__btn-check {
  line-height: 39px;
  background: linear-gradient(261.65deg, #fde6ab -3.95%, #f0dca8 109.44%);
  font-weight: 600;
  font-size: 18px;
}

.goods__check {
  position: static;
  border-radius: 30px;
  width: 46px;
  height: 46px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  background: #fff;
}

.goods__check svg {
  width: 20px;
  height: 20px;
}

.goods__btn_orange {
  border-radius: 30px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  font-family: sans-serif;
  text-transform: uppercase;
  color: #fff;
  background: linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);
}

.spoiler:hover .spoiler__list {
  display: block;
}

.spoiler:hover::before {
  transform: rotate(180deg);
  transition: all 0.8s ease 0s;
}

.spoiler__title {
  padding: 0 20px;
  line-height: 30px;
}

.spoiler__list {
  display: none;
  overflow-y: scroll;
  padding-right: 10px;
  height: 100px;
}

.spoiler__list::-webkit-scrollbar {
  width: 5px;
  background: transparent;
}

.spoiler__list::-webkit-scrollbar-thumb {
  border-radius: 30px;
  background: #E5E5E5;
}

.spoiler__item {
  padding: 0 20px;
  line-height: 30px;
}

.spoiler__item:hover {
  border-radius: 0px 10px 10px 0px;
  background: #F3F8FC;
}

.checkboxes {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 22px;
}

.checkboxes__item {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.checkboxes__item:not(:last-child) {
  margin-bottom: 13px;
}

.checkboxes__input {
  display: none;
}

.checkboxes__input:checked + .checkboxes__fake::before {
  opacity: 1;
}

.checkboxes__fake {
  position: relative;
  display: inline-block;
  border-radius: 5px;
  width: 20px;
  height: 20px;
  background: #DEDEDE;
}

.checkboxes__fake::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 5px;
  width: 15px;
  height: 15px;
  background: linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease 0s;
}

.checkboxes__volume {
  margin-left: 10px;
  font-weight: 600;
  font-size: 16px;
}

.counter {
  display: flex;
  align-items: center;
  border-radius: 30px;
  padding: 0 13px;
  background: #E2E9F0;
}

.counter__btn {
  background: #E2E9F0;
}

.counter__count {
  padding: 0 5px;
  width: 30px;
  text-align: center;
  background: #E2E9F0;
}
